'use client'

import { useState, useEffect } from 'react'
import { useRouter, usePathname } from 'next/navigation'
import Link from 'next/link'
import {
  HomeIcon,
  TicketIcon,
  BookOpenIcon,
  ComputerDesktopIcon,
  ArrowsRightLeftIcon,
  ChartBarIcon,
  UsersIcon,
  Cog6ToothIcon,
  ClockIcon,
  ArrowRightOnRectangleIcon,
  Bars3Icon,
  XMarkIcon,
} from '@heroicons/react/24/outline'
import {
  HomeIcon as HomeIconSolid,
  TicketIcon as TicketIconSolid,
  BookOpenIcon as BookOpenIconSolid,
  ComputerDesktopIcon as ComputerDesktopIconSolid,
  ArrowsRightLeftIcon as ArrowsRightLeftIconSolid,
  ChartBarIcon as ChartBarIconSolid,
  UsersIcon as UsersIconSolid,
  Cog6ToothIcon as Cog6ToothIconSolid,
  ClockIcon as ClockIconSolid,
} from '@heroicons/react/24/solid'

interface NavItem {
  href: string
  label: string
  icon: any
  iconSolid: any
  roles?: string[]
  section?: string
}

export default function SideNav() {
  const router = useRouter()
  const pathname = usePathname()
  const [user, setUser] = useState<any>(null)
  const [isExpanded, setIsExpanded] = useState(false)
  const [hoveredItem, setHoveredItem] = useState<string | null>(null)

  useEffect(() => {
    const storedUser = localStorage.getItem('user')
    if (storedUser) {
      try {
        setUser(JSON.parse(storedUser))
      } catch (e) {
        // Invalid user data
      }
    }

    // Load saved preference
    const savedExpanded = localStorage.getItem('sideNavExpanded')
    if (savedExpanded === 'true') {
      setIsExpanded(true)
    }
  }, [])

  // Don't show navigation on login/register/landing pages
  if (pathname === '/' || pathname === '/login' || pathname === '/register' || pathname?.startsWith('/reset-password')) {
    return null
  }

  const handleLogout = () => {
    localStorage.removeItem('accessToken')
    localStorage.removeItem('refreshToken')
    localStorage.removeItem('user')
    router.push('/login')
  }

  const toggleExpand = () => {
    const newExpanded = !isExpanded
    setIsExpanded(newExpanded)
    localStorage.setItem('sideNavExpanded', String(newExpanded))
  }

  const isAdmin = user?.roles?.includes('ADMIN')
  const isManager = user?.roles?.includes('IT_MANAGER')
  const isAgent = user?.roles?.includes('AGENT')

  const navItems: NavItem[] = [
    { href: '/dashboard', label: 'Dashboard', icon: HomeIcon, iconSolid: HomeIconSolid },
    { href: '/tickets', label: 'Tickets', icon: TicketIcon, iconSolid: TicketIconSolid },
    { href: '/kb', label: 'Knowledge Base', icon: BookOpenIcon, iconSolid: BookOpenIconSolid },
    { href: '/assets', label: 'Assets', icon: ComputerDesktopIcon, iconSolid: ComputerDesktopIconSolid, roles: ['AGENT', 'IT_MANAGER', 'ADMIN'] },
    { href: '/changes', label: 'Changes', icon: ArrowsRightLeftIcon, iconSolid: ArrowsRightLeftIconSolid, roles: ['AGENT', 'IT_MANAGER', 'ADMIN'] },
    { href: '/reports', label: 'Reports', icon: ChartBarIcon, iconSolid: ChartBarIconSolid, roles: ['IT_MANAGER', 'ADMIN'] },
  ]

  const adminItems: NavItem[] = [
    { href: '/admin/users', label: 'Users', icon: UsersIcon, iconSolid: UsersIconSolid, section: 'admin' },
    { href: '/admin/config', label: 'Configuration', icon: Cog6ToothIcon, iconSolid: Cog6ToothIconSolid, section: 'admin' },
    { href: '/admin/sla', label: 'SLA Management', icon: ClockIcon, iconSolid: ClockIconSolid, section: 'admin' },
  ]

  const canAccess = (item: NavItem) => {
    if (!item.roles) return true
    if (!user?.roles) return false
    return item.roles.some((role: string) => user.roles.includes(role))
  }

  const filteredNavItems = navItems.filter(canAccess)
  const filteredAdminItems = adminItems.filter(canAccess)

  const isActive = (href: string) => {
    if (href === '/dashboard') {
      return pathname === '/dashboard'
    }
    return pathname?.startsWith(href)
  }

  const navWidth = isExpanded ? 240 : 72

  useEffect(() => {
    document.documentElement.style.setProperty('--side-nav-width', `${navWidth}px`)
  }, [navWidth])

  return (
    <nav
      style={{
        position: 'fixed',
        left: 0,
        top: 0,
        bottom: 0,
        width: `${navWidth}px`,
        backgroundColor: 'var(--bg-secondary)',
        borderRight: '1px solid var(--border-color)',
        display: 'flex',
        flexDirection: 'column',
        zIndex: 1000,
        transition: 'width 0.3s ease',
        overflow: 'hidden',
      }}
    >
        {/* Header */}
        <div
          style={{
            padding: isExpanded ? '1rem' : '1rem 0.75rem',
            borderBottom: '1px solid var(--border-color)',
            display: 'flex',
            alignItems: 'center',
            justifyContent: isExpanded ? 'space-between' : 'center',
            minHeight: '64px',
          }}
        >
          {isExpanded && (
            <Link
              href="/dashboard"
              style={{
                display: 'flex',
                alignItems: 'center',
                gap: '0.5rem',
                textDecoration: 'none',
                color: 'var(--text-primary)',
                fontSize: '1.25rem',
                fontWeight: 'bold',
              }}
            >
              <TicketIcon className="w-6 h-6" style={{ color: 'var(--accent-primary)' }} />
              <span>ITSM</span>
            </Link>
          )}
          {!isExpanded && (
            <TicketIcon className="w-6 h-6" style={{ color: 'var(--accent-primary)' }} />
          )}
          <button
            onClick={toggleExpand}
            style={{
              padding: '0.5rem',
              backgroundColor: 'transparent',
              border: 'none',
              color: 'var(--text-primary)',
              cursor: 'pointer',
              borderRadius: '6px',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              transition: 'background-color 0.2s',
            }}
            onMouseEnter={(e) => {
              e.currentTarget.style.backgroundColor = 'var(--bg-tertiary)'
            }}
            onMouseLeave={(e) => {
              e.currentTarget.style.backgroundColor = 'transparent'
            }}
            aria-label={isExpanded ? 'Collapse navigation' : 'Expand navigation'}
          >
            {isExpanded ? (
              <XMarkIcon className="w-5 h-5" />
            ) : (
              <Bars3Icon className="w-5 h-5" />
            )}
          </button>
        </div>

        {/* Navigation Items */}
        <div
          style={{
            flex: 1,
            overflowY: 'auto',
            padding: '0.5rem',
            display: 'flex',
            flexDirection: 'column',
            gap: '0.25rem',
          }}
        >
          {filteredNavItems.map((item) => {
            const active = isActive(item.href)
            const Icon = active ? item.iconSolid : item.icon
            const isHovered = hoveredItem === item.href

            return (
              <div key={item.href} style={{ position: 'relative' }}>
                <Link
                  href={item.href}
                  style={{
                    display: 'flex',
                    alignItems: 'center',
                    gap: isExpanded ? '0.75rem' : '0',
                    padding: '0.75rem',
                    textDecoration: 'none',
                    color: active ? 'var(--accent-primary)' : 'var(--text-primary)',
                    borderRadius: '8px',
                    backgroundColor: active ? 'rgba(59, 130, 246, 0.1)' : 'transparent',
                    transition: 'all 0.2s',
                    position: 'relative',
                  }}
                  onMouseEnter={() => setHoveredItem(item.href)}
                  onMouseLeave={() => setHoveredItem(null)}
                  onFocus={() => setHoveredItem(item.href)}
                  onBlur={() => setHoveredItem(null)}
                >
                  <Icon
                    className="w-5 h-5"
                    style={{
                      flexShrink: 0,
                      minWidth: '20px',
                    }}
                  />
                  {isExpanded && (
                    <span
                      style={{
                        fontSize: '0.875rem',
                        fontWeight: active ? 600 : 400,
                        whiteSpace: 'nowrap',
                        overflow: 'hidden',
                        textOverflow: 'ellipsis',
                      }}
                    >
                      {item.label}
                    </span>
                  )}
                </Link>

                {/* Tooltip when collapsed */}
                {!isExpanded && isHovered && (
                  <div
                    style={{
                      position: 'absolute',
                      left: '100%',
                      top: '50%',
                      transform: 'translateY(-50%)',
                      marginLeft: '0.75rem',
                      padding: '0.5rem 0.75rem',
                      backgroundColor: 'var(--bg-tertiary)',
                      color: 'var(--text-primary)',
                      borderRadius: '6px',
                      fontSize: '0.875rem',
                      whiteSpace: 'nowrap',
                      zIndex: 1001,
                      boxShadow: '0 4px 12px rgba(0, 0, 0, 0.3)',
                      border: '1px solid var(--border-color)',
                      pointerEvents: 'none',
                    }}
                  >
                    {item.label}
                    <div
                      style={{
                        position: 'absolute',
                        right: '100%',
                        top: '50%',
                        transform: 'translateY(-50%)',
                        border: '6px solid transparent',
                        borderRightColor: 'var(--bg-tertiary)',
                      }}
                    />
                  </div>
                )}
              </div>
            )
          })}

          {/* Admin Section */}
          {isAdmin && filteredAdminItems.length > 0 && (
            <>
              {isExpanded && (
                <div
                  style={{
                    padding: '1rem 0.75rem 0.5rem',
                    fontSize: '0.75rem',
                    fontWeight: 600,
                    color: 'var(--text-secondary)',
                    textTransform: 'uppercase',
                    letterSpacing: '0.05em',
                    marginTop: '0.5rem',
                  }}
                >
                  Admin
                </div>
              )}
              {filteredAdminItems.map((item) => {
                const active = isActive(item.href)
                const Icon = active ? item.iconSolid : item.icon
                const isHovered = hoveredItem === item.href

                return (
                  <div key={item.href} style={{ position: 'relative' }}>
                    <Link
                      href={item.href}
                      style={{
                        display: 'flex',
                        alignItems: 'center',
                        gap: isExpanded ? '0.75rem' : '0',
                        padding: '0.75rem',
                        textDecoration: 'none',
                        color: active ? 'var(--accent-primary)' : 'var(--text-primary)',
                        borderRadius: '8px',
                        backgroundColor: active ? 'rgba(59, 130, 246, 0.1)' : 'transparent',
                        transition: 'all 0.2s',
                      }}
                      onMouseEnter={() => setHoveredItem(item.href)}
                      onMouseLeave={() => setHoveredItem(null)}
                      onFocus={() => setHoveredItem(item.href)}
                      onBlur={() => setHoveredItem(null)}
                    >
                      <Icon
                        className="w-5 h-5"
                        style={{
                          flexShrink: 0,
                          minWidth: '20px',
                        }}
                      />
                      {isExpanded && (
                        <span
                          style={{
                            fontSize: '0.875rem',
                            fontWeight: active ? 600 : 400,
                            whiteSpace: 'nowrap',
                            overflow: 'hidden',
                            textOverflow: 'ellipsis',
                          }}
                        >
                          {item.label}
                        </span>
                      )}
                    </Link>

                    {/* Tooltip when collapsed */}
                    {!isExpanded && isHovered && (
                      <div
                        style={{
                          position: 'absolute',
                          left: '100%',
                          top: '50%',
                          transform: 'translateY(-50%)',
                          marginLeft: '0.75rem',
                          padding: '0.5rem 0.75rem',
                          backgroundColor: 'var(--bg-tertiary)',
                          color: 'var(--text-primary)',
                          borderRadius: '6px',
                          fontSize: '0.875rem',
                          whiteSpace: 'nowrap',
                          zIndex: 1001,
                          boxShadow: '0 4px 12px rgba(0, 0, 0, 0.3)',
                          border: '1px solid var(--border-color)',
                          pointerEvents: 'none',
                        }}
                      >
                        {item.label}
                        <div
                          style={{
                            position: 'absolute',
                            right: '100%',
                            top: '50%',
                            transform: 'translateY(-50%)',
                            border: '6px solid transparent',
                            borderRightColor: 'var(--bg-tertiary)',
                          }}
                        />
                      </div>
                    )}
                  </div>
                )
              })}
            </>
          )}
        </div>

        {/* User Menu */}
        <div
          style={{
            padding: '0.5rem',
            borderTop: '1px solid var(--border-color)',
          }}
        >
          {user && (
            <div
              style={{
                display: 'flex',
                alignItems: 'center',
                gap: isExpanded ? '0.75rem' : '0',
                padding: '0.75rem',
                borderRadius: '8px',
                backgroundColor: 'var(--bg-primary)',
              }}
            >
              <div
                style={{
                  width: '32px',
                  height: '32px',
                  borderRadius: '50%',
                  backgroundColor: 'var(--accent-primary)',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  color: 'white',
                  fontSize: '0.875rem',
                  fontWeight: 600,
                  flexShrink: 0,
                }}
              >
                {(user.firstName?.[0] || user.email?.[0] || 'U').toUpperCase()}
              </div>
              {isExpanded && (
                <div style={{ flex: 1, minWidth: 0 }}>
                  <div
                    style={{
                      fontSize: '0.875rem',
                      fontWeight: 500,
                      color: 'var(--text-primary)',
                      whiteSpace: 'nowrap',
                      overflow: 'hidden',
                      textOverflow: 'ellipsis',
                    }}
                  >
                    {user.firstName && user.lastName
                      ? `${user.firstName} ${user.lastName}`
                      : user.email?.split('@')[0] || 'User'}
                  </div>
                  <div
                    style={{
                      fontSize: '0.75rem',
                      color: 'var(--text-secondary)',
                      whiteSpace: 'nowrap',
                      overflow: 'hidden',
                      textOverflow: 'ellipsis',
                    }}
                  >
                    {user.email}
                  </div>
                </div>
              )}
            </div>
          )}
          <button
            onClick={handleLogout}
            style={{
              width: '100%',
              display: 'flex',
              alignItems: 'center',
              gap: isExpanded ? '0.75rem' : '0',
              justifyContent: isExpanded ? 'flex-start' : 'center',
              padding: '0.75rem',
              marginTop: '0.5rem',
              backgroundColor: 'transparent',
              border: 'none',
              color: 'var(--text-secondary)',
              cursor: 'pointer',
              borderRadius: '8px',
              transition: 'all 0.2s',
            }}
            onMouseEnter={(e) => {
              e.currentTarget.style.backgroundColor = 'rgba(239, 68, 68, 0.1)'
              e.currentTarget.style.color = 'var(--error)'
            }}
            onMouseLeave={(e) => {
              e.currentTarget.style.backgroundColor = 'transparent'
              e.currentTarget.style.color = 'var(--text-secondary)'
            }}
            aria-label="Sign out"
          >
            <ArrowRightOnRectangleIcon className="w-5 h-5" style={{ flexShrink: 0 }} />
            {isExpanded && <span style={{ fontSize: '0.875rem' }}>Sign Out</span>}
          </button>
        </div>
      </nav>
  )
}

